
.slide-trans-enter-active {
  transition: all .5s;
}
.slide-trans-enter {
  -webkit-transform: translateX(810px);
          transform: translateX(810px);
}
.slide-trans-old-leave-active {
  transition: all .5s;
  -webkit-transform: translateX(-810px);
          transform: translateX(-810px);
}
.slide-show {
  width: 810px;
  height: 500px;
  position: relative;
  overflow: hidden;
  margin-bottom: 10px;
}
.slide-img {
  width: 100%;
}
.slide-img img {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0
}
.slide-show h2 {
  position: absolute;
  height: 24px;
  line-height: 24px;
  bottom: 0;
  left: 15px;
  color: #eee;
  z-index: 5;
}
.slide-show ul {
  text-align: right;
  padding-right: 15px;
  box-sizing: border-box;
}
.slide-show ul li {
  display: inline-block;
  vertical-align: top;
  line-height: 24px;
  height: 24px;
  font-size: 14px;
  color:#eee;
  cursor: pointer;
  padding: 0 10px;
}
.slide-pages {
  position: absolute;
  width: 100%;
  height: 24px;
  background: rgba(0,0,0,0.8);
  bottom:0;
}
.on {
  text-decoration: underline
}



