
.order-wrap {
  width: 1200px;
  margin: 0 auto;
  overflow: hidden;
  min-height: 800px;
}
.order-list-choose {
  margin-top: 15px;
}
.order-list-option{
  display: inline-block;
  padding-left: 15px;
}
.order-list-option:first-child{
  padding-left: 0;
}
.order-query {
  height: 25px;
  line-height: 25px;
  border: 1px solid #e3e3e3;
  outline: none;
  text-indent: 10px;
}
.order-list-table{
  margin-top: 20px;
}
.order-list-table table{
  width: 100%;
  background: #fff;
}
.order-list-table td,
.order-list-table th {
  border: 1px solid #e3e3e3;
  text-align: center;
  padding: 10px 0;
}
.order-list-table th {
  background: #4fc08d;
  color: #fff;
  border: 1px solid #4fc08d;
  cursor: pointer;
}
.order-list-table th.active {
  background: #35495e;
}



