
.detail-wrapper{
  width: 1200px;
  margin: 0 auto;
  overflow: hidden;
  height: 800px;
}
.detail-left {
  float: left;
  width: 200px;
  background: #fff;
  margin-right: 10px;
  margin-top: 10px;
}
.product-board img{
  display: block;
  margin: 0 auto;
  padding: 20px 0;
}
.product-board ul{
  padding: 10px 0;
}
.product-board ul li {
  padding: 10px 15px;
  cursor: pointer;
}
.product-board ul li:hover,li.active {
  background: #41c08d;
  color: #fff;
}
.detail-right{
  float: left;
  margin-top: 10px;
}
.sales-board{
  width: 910px;
}
.sales-board-intro,.sales-board-des{
  background:#fff;
  padding: 0 20px;
}
.sales-board-intro h2,.sales-board-des h2 {
  font-weight: normal;
  padding-top: 20px;
}
.sales-board-intro p,.sales-board-des p{
  padding: 20px 0;
  font-size: 12px;
  opacity: 0.8;
  line-height: 20px;
}
.sales-board-form {
  padding: 0 20px 30px;
  background:#fff;
}
.sales-board-line {
  width: 100%;
  height: 30px;
  padding-bottom: 10px;
  clear: both;
}
.sales-board-line-left {
  float: left;
  width: 100px;
  font-size: 13px;
  line-height: 30px;
  font-weight: bold;
  opacity: .8;
  overflow: hidden;
}
.sales-board-line-right {
}
.button {
  width: 100px;
  margin-left: 100px;
  background: #41b883;
  color: #fff;
  text-align: center;
  padding: 6px 0 5px 0;
  border-radius: 1px;
}
.sales-board-des {
  margin-top: 10px;
  padding-bottom: 40px;
}
.sales-board-des h3 {
  font-size: 15px;
  line-height: 20px;
  padding: 5px 0;
}
.sales-board-des p {
  font-size: 12px;
  line-height: 24px;
}
.sales-board-des ul{
  padding-bottom: 10px;
}
.sales-board-des ul li{
  font-size: 13px;
  line-height: 20px;
}
.sales-board-table {
  width: 100%;
}
.ui-table-row{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  font-size: 12px;
  opacity: .9;
}
.ui-table-row td{
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  padding: 15px;
}



